body{
    color: rgb(255, 255, 255);
    line-height: 2.5;
    font-size: 1em;
    text-shadow: 1.5px 1.5px rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
}

body::before{
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background: rgb(41, 40, 40);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.welcome-page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.6);
    border-radius: 0.50rem;
    margin: auto auto auto auto;
    padding: 150px;
}

.welcome-form{
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 5px;
}

.inputs{
    text-align: center;
}

.answer-page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.6);
    border-radius: 0.50rem;
    margin: auto auto auto auto;
    padding: 150px;
}